.board {

}

.tileRow {
	display: flex;
    align-items: center;

	.tileImage {
		width: 5em;
		height: 5em;
	}
}

.tileButton {
	cursor: pointer;
	width: 5em;
	height: 5em;

	.tileTitle {
		position: absolute;
		background: white;
		width: 1.5em;
		height: 1.5em;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		margin-top: 5px;
		margin-left: 5px;
	}

	.activeTileButton {
		border: 2px solid red;
	}
}

.tileButtons {
	display: flex;
}