$backgroundColor: rgb(255, 245, 159);

.tile {
    width: 3em;
    height: 3em;
	border: 1px solid black;
	display: inline-flex;
    align-items: center;
	justify-content: center;
	cursor: pointer;

	-webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: -moz-none;
    -o-user-select: none;
    user-select: none;

	&:hover {
		background: $backgroundColor;
	}
}
